import React, { useState } from 'react';
import { MdAddShoppingCart } from 'react-icons/md';
import { LuPictureInPicture } from "react-icons/lu";
import { HiOutlineShoppingCart } from "react-icons/hi";
import { IoClose } from 'react-icons/io5';
import { GoStarFill, GoPeople } from "react-icons/go";
import { FaTrophy } from 'react-icons/fa';

import { AiOutlineClose } from 'react-icons/ai';
import { GoHorizontalRule } from "react-icons/go";
import { GoEyeClosed } from "react-icons/go";
import './playlistsetting.css'
const EcommerceCard = ({ videoData, buttonValues, setButtonValues, settings, colorStates, isSeenEnabled, setIsSeenEnabled }) => {

  const [quantity, setQuantity] = useState(1);
  const handleIncrement = () => {
    setQuantity(quantity + 1);
  };

  const handleDecrement = () => {
    if (quantity > 1) {
      setQuantity(quantity - 1);
    }
  };
  const boxHeight = `calc(100% - 150px)`;
  return (
    <div className="container-fluid d-flex justify-content-center align-items-center p-2 pt-5 pb-5 h-100" style={{ backgroundColor: "#F8F8FF", borderRadius: "5px" }}>
      <div className='position-relative col-12 col-md-6' >
        {!isSeenEnabled ? (
          <>
            <video className="card-img-top" id="image-top" style={{ borderRadius: "20px", }} poster={videoData.cover_image}>
              <source src={videoData.video_url} type="video/mp4" />
              Your browser does not support the video tag.
            </video>
            <div className='position-absolute top-0 end-0 gap-2 pe-1 d-flex flex-row align-items-end mt-2'>
              <LuPictureInPicture title='Pip Mode' color='white' size={22} className='icon' />
              <HiOutlineShoppingCart title='Cart' color='white' size={22} className='icon' />
              <IoClose title='Close' color='white' size={22} className='icon' />
            </div>
            <div className='position-absolute outer' id="outer" style={{ bottom: "0", width: "100%", background: "white", height: "70%" }}>

              <div className='row align-items-center justify-content-center' style={{ height: "120px" }}>
                <GoHorizontalRule size={35} />
                <p className='mb-0 ps-4 mt-2'>Review </p>
                <span className='ps-4'><GoStarFill color='#0C781E' /> 4.5 |<span><GoPeople /> 200</span></span>
                <hr className='mt-1 me-2 ms-2' style={{ width: '90%' }} />
              </div>

              <div className='box position-absolute' style={{ bottom: "10%", overflowX: "hidden", height: boxHeight, width: "100%", overflowY: "auto" }}>
                <div className='d-flex justify-content-center'>
                  <img src={videoData.image} style={{ height: "15%", width: "auto", borderRadius: "8px" }} alt="video thumbnail" />
                </div>
                <div className='ms-2 '>
                  <FaTrophy color='#FFCE31' size={16} /><span className='ps-1 mt-1' style={{ color: "#FF730E", fontSize: "12px" }}>Best seller</span>
                  <h4 style={{ fontSize: "18px" }}>{videoData.video_title}</h4>
                  <p>
                    <span style={{ fontSize: '24px' }}>${videoData.price}</span>
                    <del className="text-muted" style={{ fontSize: '18px', color: '#747477' }}>${videoData.discount_price}</del>
                    {/* <div className='row ps-2 mt-2'>
  //                     <div className='col-12'>
  //                         <label htmlFor="capacity" className="form-label" style={{fontSize:"16px"}}>Capacity</label>
  //                         <div className='d-flex gap-2'>
  //                           {['1', '2', '3'].map((capacity) => (
  //                             <button
  //                               key={capacity}
  //                               className={`btn btn-outline-secondary ${selectedCapacity === capacity ? 'btn-secondary text-white' : ''}`}
  //                               onClick={() => handleCapacitySelect(capacity)}
  //                             >
  //                               {capacity}
  //                             </button>
  //                           ))}
  //                         </div>
  //                         </div>
  //                     </div> */}
                    {settings.isProductquantityEnabled && (
                      <div className='row ps-1'>
                        <div className='col-12'>
                          <label htmlFor="quantity" className="form-label">Quantity</label>
                          <div className='row ps-2'>
                            <p>
                              <button className='btn btn-outline-secondary rounded-circle btn_quantity1' type="button" onClick={handleDecrement} disabled={quantity === 1} >-</button>
                              <span className='p-3'>{quantity}</span>
                              <button className='btn btn-outline-secondary rounded-circle btn_quantity2' type="button" onClick={handleIncrement} disabled={quantity === 20}>+</button>
                            </p>
                          </div>

                        </div>
                      </div>)}
                  </p>
                </div>

              </div>
              <div className='position-absolute bottom-0 w-100' style={{ height: "20px" }}>
                <div className='d-flex align-items-center ms-2 mt-1 mb-2' style={{ height: "5%" }}>
                  {settings.isAddtoCartEnabled && (<button className='btn btn-sm  me-1' style={{
                    fontSize: "12px", width: "95%", color: colorStates.find(initialColors => initialColors.id === 7)?.color, border: "1px solid black", whiteSpace: "nowrap",
                    overflow: "hidden",
                    textOverflow: "ellipsis"
                  }}><MdAddShoppingCart size={18} style={{ verticalAlign: "bottom" }} />{buttonValues.find(btn => btn.id === 9)?.value}</button>)}
                  {settings.isBuyEnabled && (<button className='btn btn-sm k me-1' style={{
                    fontSize: "12px", width: "95%", backgroundColor: colorStates.find(initialColors => initialColors.id === 6)?.color, color: colorStates.find(initialColors => initialColors.id === 5)?.color, overflow: "hidden",
                    textOverflow: "ellipsis", whiteSpace: "nowrap"
                  }}>{buttonValues.find(btn => btn.id === 8)?.value}
                  </button>)}
                </div>
              </div>

            </div>
          </>
        ) : (
          <>
            <video className="card-img-top" style={{ borderRadius: "20px" }} poster={videoData.cover_image}>
              <source src={videoData.video_url} type="video/mp4" />
              Your browser does not support the video tag.
            </video>
            <div className=' symbols position-absolute end-0 d-flex flex-row align-items-end mt-2'>

              <LuPictureInPicture color='white' size={22} />

              <HiOutlineShoppingCart color='white' size={22} />

              <IoClose color='white' size={22} />

            </div>

            <div className='row align-items-center justify-content-center'>
              <div className='boxs position-absolute' style={{ bottom: "40%", background: "white", borderRadius: "10px", width: "95%" }}>
                <AiOutlineClose onClick={() => { setIsSeenEnabled(0) }} style={{ position: 'absolute', top: '8px', right: '8px', fontSize: '20px', cursor: 'pointer' }} />
                <div className='row mt-3 card-title-add'>
                  <h5>{buttonValues.find(btn => btn.id === 10)?.value}</h5>
                </div>
                <div className='row'>
                  <div className='col-5 mt-3 ms-2 mb-3'>
                    <button className='btn btn-light card-button-add'>{buttonValues.find(btn => btn.id === 11)?.value}</button>
                  </div>
                  <div className='col-6 mt-3 mb-3'>
                    <button className='btn btn-dark card-button-add'>{buttonValues.find(btn => btn.id === 12)?.value}</button>
                  </div>
                </div>
              </div></div>
          </>
        )}
      </div>
    </div >
  );
};

export default EcommerceCard;