import React, { useState } from 'react'

const Othersetting = () => {
    const [VideoDisclaimer, SetVideoDisclaimer] = useState(false);

    const handleChange = (e) => {
        SetVideoDisclaimer(e.target.checked);
    };
    return (
        <div style={{ height: "53vh", overflow: "auto", overflowX: "hidden" }}>
            <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 m-1 p-2 m-1 p-2">
                <div>
                    <label className="fs-18 form-check-label ps-3" htmlFor="priceSwitch">
                        Integrate Stamp
                        <br />

                    </label>
                </div>
                <div>
                    <input
                        className="form-check-input switch-lg"
                        type="checkbox"
                        role="switch"

                    />
                </div>
            </div>
            <div style={{ backgroundColor: VideoDisclaimer ? '#F5F5F5' : 'white', borderRadius: "10px" }}>

                <div className="form-check form-switch d-flex justify-content-between align-items-center m-1 p-2" >
                    <div>
                        <label className="fs-18 form-check-label ps-3" htmlFor="Shoppable">
                            Video Disclaimer
                        </label>
                    </div>
                    <div>
                        <input
                            className="form-check-input switch-lg"
                            type="checkbox"
                            role="switch"
                            id="Shoppable"

                            checked={VideoDisclaimer}
                            onChange={handleChange}
                        />
                    </div>
                </div>
                {VideoDisclaimer &&
                    <div className='row ms-2 '>
                        <div className="form-group mt-2 ">

                            <div className='input-group '>
                                <input
                                    type="text"
                                    className="form-control m-2"
                                    style={{ backgroundColor: "#F5F5F5" }}
                                    placeholder='Enter Video Disclaimer text'
                                />

                            </div>
                        </div>
                    </div>}
            </div>

            <div className="m-1 p-2" style={{ backgroundColor: '#F5F5F5', borderRadius: "10px" }}>

                <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0 " >
                    <div>
                        <label className="fs-18 form-check-label ps-3" htmlFor="Shoppable">
                            Custom Add to Cart Class
                        </label>
                    </div>
                </div>

                <div className='row'>
                    <div className="form-group mt-2 ">

                        <div className='input-group '>
                            <input
                                type="text"
                                className="form-control m-2"
                                style={{ backgroundColor: "#F5F5F5" }}
                                placeholder='ajax_cart_class'
                            />

                        </div>
                    </div>
                </div>
            </div>
            <div className="m-1 p-2" style={{ backgroundColor: '#F5F5F5', borderRadius: "10px" }}>

                <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0" >
                    <div>
                        <label className="fs-18 form-check-label ps-3" htmlFor="Shoppable">
                            Add to Cart End Point
                        </label>
                    </div>
                </div>

                <div className='row'>
                    <div className="form-group mt-2 ">

                        <div className='input-group '>
                            <input
                                type="text"
                                className="form-control m-2"
                                style={{ backgroundColor: "#F5F5F5" }}
                                placeholder='Enter End point'
                            />

                        </div>
                    </div>
                </div>
            </div>
            <div className='m-1 p-2' style={{ backgroundColor: '#F5F5F5', borderRadius: "10px" }}>

                <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0" >
                    <div>
                        <label className="fs-18 form-check-label ps-3" htmlFor="Shoppable">
                            Dot Class
                        </label>
                    </div>
                </div>

                <div className='row  '>
                    <div className="form-group mt-2 ">

                        <div className='input-group '>
                            <input
                                type="text"
                                className="form-control m-2"
                                style={{ backgroundColor: "#F5F5F5" }}
                                placeholder='xoo-wsc-item-count'
                            />

                        </div>
                    </div>
                </div>
            </div>
            <div className='m-1 p-2' style={{ backgroundColor: '#F5F5F5', borderRadius: "10px" }}>

                <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0" >
                    <div>
                        <label className="fs-18 form-check-label ps-3" htmlFor="Shoppable">
                            Custom Cart Redirection Link
                        </label>
                    </div>
                </div>

                <div className='row '>
                    <div className="form-group mt-2 ">

                        <div className='input-group '>
                            <input
                                type="text"
                                className="form-control m-2"
                                style={{ backgroundColor: "#F5F5F5" }}
                                placeholder='cart'
                            />

                        </div>
                    </div>
                </div>
            </div>
        </div>

    )
}

export default Othersetting