import React, { useEffect, useState } from 'react';
import { Button, Col, Container, Nav, Row, Tab } from 'react-bootstrap';
import PlaylistSetting from './PlaylistSetting';
import './playlistsetting.css';
import Ecommerce from './Ecommerce';
import Productdetailpage from './Productdetailpage';
import VideoplayerSetting from './VideoplayerSetting';
import PlaylistCard from './PlaylistCard';
import VideoPlayerCard from './VideoPlayerCard';
import EcommerceCard from './EcommerceCard';
import axios from 'axios';
import { useSelector } from 'react-redux';
import { toast } from 'react-toastify';
import Othersetting from './Othersetting';

const VideoSettingsTabs = () => {
    const [activeTab, setActiveTab] = useState('playlist');
    const [fetchedVideoSettingsData, setFetchedVideoSettingsData] = useState(null)

    const [settings, setSettings] = useState({
        isMuteEnabled: false,
        ispipEnabled: false,
        ispreviewEnabled: false,
        isvideodownloadEnabled: false,
        isproductTagEnabled: false,
        iscommentEnabled: false,
        isproductreviewEnabled: false,
        isbuynowEnabled: false,
        isctaEnabled: false,
        isaddtocartEnabled: false,
        isBuyEnabled: false,
        isAddtoCartEnabled: false,
        isviewcartEnabled: false,
        isProductquantityEnabled: false,
        isCardpopup: false,
        isAutoplayEnabled: false,
        isViewsEnabled: false,
        isTagEnabled: false,
        isProductEnabled: false,
        isPriceEnabled: true,
        isVideoTitleEnabled: false,

    })
    const [isSeenEnabled, setIsSeenEnabled] = useState(0)
    const [isEdited, setIsedited] = useState(false)
    const getObjectById = (id, items) => {
        return items.find(item => item.id === id);
    };

    // isvideodownloadEnabled
    // {
    //     isMuteEnabled: autoPlayMuteUn,
    //     ispipEnabled: pipMode,
    //     ispreviewEnabled: cust.next_previous_preview,
    //     isvideodownloadEnabled: cust.download_icon,
    //     isproductTagEnabled: false,
    //     iscommentEnabled: false,
    //     isproductreviewEnabled: false,
    //     isbuynowEnabled: cust.buy_now,
    //     isctaEnabled: false,
    //     isaddtocartEnabled: cust.add_to_cart,

    //     isBuyEnabled: false,
    //     isAddtoCartEnabled: false,
    //     isviewcartEnabled: false,
    //     isProductquantityEnabled: false,
    //     isCardpopup: false,
    //     isAutoplayEnabled: false,
    //     isViewsEnabled: false,
    //     isTagEnabled: false,
    //     isProductEnabled: false,
    //     isPriceEnabled: cust.product_price_status,
    //     isVideoTitleEnabled: cust.video_title,
    //     isSeenEnabled: false,
    // }
    const user = useSelector(state => state.auth.user);
    const [colorPickers, setColorPickers] = useState([
        { id: 1, isVisible: false, color: '#fff' },
        { id: 2, isVisible: false, color: '#56b280' },
        { id: 3, isVisible: false, color: '#fff' },
        { id: 4, isVisible: false, color: '#fff' },
    ]);

    const initialColors = [
        { id: 5, color: '#000', showColorPicker: false },
        { id: 6, color: '#000', showColorPicker: false },
        { id: 7, color: '#000', showColorPicker: false }
    ];

    const [colorStates, setColorStates] = useState(initialColors);

    const [buttonValues, setButtonValues] = useState([
        { id: 8, value: 'Buy Now' }, // Example initial value
        { id: 9, value: 'Add to Cart' }, // Example initial value
        { id: 10, value: 'Added to Cart' },// Example initial value
        { id: 11, value: 'View Cart' }, // Example initial value
        { id: 12, value: 'Continue Shopping' },
    ]);

    const handleSwitchChnage = (updatedsettings) => {
        if (fetchedVideoSettingsData) {

            const updatedData = {
                ...fetchedVideoSettingsData,
                autoPlayMuteUn: updatedsettings.isMuteEnabled ? 1 : 0,
                pipMode: updatedsettings.ispipEnabled ? 1 : 0,
                brandCustomization: {
                    ...fetchedVideoSettingsData.brandCustomization,
                    next_previous_preview: updatedsettings.ispreviewEnabled ? 1 : 0,
                    download_icon: updatedsettings.isvideodownloadEnabled ? 1 : 0,
                    comment_mode: updatedsettings.iscommentEnabled ? 1 : 0,
                    product_reviews: updatedsettings.isproductreviewEnabled ? 1 : 0,
                    cta_on_tile: updatedsettings.isctaEnabled ? 1 : 0,
                    buy_now: updatedsettings.isBuyEnabled ? 1 : 0,
                    add_to_cart: updatedsettings.isAddtoCartEnabled ? 1 : 0,
                    view_cart: updatedsettings.isviewcartEnabled ? 1 : 0,
                    product_qty: updatedsettings.isProductquantityEnabled ? 1 : 0,
                    show_add_to_cart_popup: updatedsettings.isCardpopup ? 1 : 0,
                    autoPlay: updatedsettings.isAutoplayEnabled ? 1 : 0, // used
                    views: updatedsettings.isViewsEnabled ? 1 : 0,// used
                    deals: updatedsettings.isTagEnabled ? 1 : 0, // used
                    product_price_status: updatedsettings.isPriceEnabled ? 1 : 0,
                    video_title: updatedsettings.isVideoTitleEnabled ? 1 : 0, // used
                    product_blog_img: updatedsettings.isProductEnabled ? 1 : 0, // used
                }
            };
            console.log(updatedData);
            setFetchedVideoSettingsData(updatedData)

        }
    };

    console.log(fetchedVideoSettingsData);
    const handleColorChangeInPlaylist = (chnageCOlors) => {
        // console.log(getObjectById(1, chnageCOlors).color);
        if (fetchedVideoSettingsData) {
            const updatedData = {
                ...fetchedVideoSettingsData,
                brandCustomization: {
                    ...fetchedVideoSettingsData.brandCustomization,
                    off_fk_color: getObjectById(1, chnageCOlors).color,
                    off_bk_color: getObjectById(2, chnageCOlors).color,
                    mrp_fk_color: getObjectById(3, chnageCOlors).color,
                    dis_fk_color: getObjectById(4, chnageCOlors).color,


                }
            };


            setFetchedVideoSettingsData(updatedData)
        }

    }
    const handleBtnValuesChnage = (chanedBtnText) => {

        if (fetchedVideoSettingsData) {
            const updatedData = {
                ...fetchedVideoSettingsData,
                brandCustomization: {
                    ...fetchedVideoSettingsData.brandCustomization,
                    buy_btn: getObjectById(8, chanedBtnText).value,
                    add_to_cart_btn: getObjectById(9, chanedBtnText).value,
                    cart_success_message: getObjectById(10, chanedBtnText).value,
                    view_cart_txt: getObjectById(11, chanedBtnText).value,
                    shopping_text: getObjectById(12, chanedBtnText).value,

                }
            };

            // Set the updated data to your state or context if necessary
            setFetchedVideoSettingsData(updatedData)
        }

    }
    const handleColorChangeInEcommerce = (chnageCOlors) => {
        if (fetchedVideoSettingsData) {
            const updatedData = {
                ...fetchedVideoSettingsData,
                brandCustomization: {
                    ...fetchedVideoSettingsData.brandCustomization,
                    front_color_buy_btn: getObjectById(5, chnageCOlors).color,
                    bk_color_buy_btn: getObjectById(6, chnageCOlors).color,
                    front_color_add_to_cart_btn: getObjectById(7, chnageCOlors).color,
                }
            };
            console.log(updateData)
            // Set the updated data to your state or context if necessary
            setFetchedVideoSettingsData(updatedData)
        }

    }
    const videoData = {
        "video_title": "Dashing Red Mini Ferrari",
        "cover_image": "https://ik.imagekit.io/coverimages/tr:w-540/1715162670.webp",
        "video_url": "https://d1g3m9ml694eqp.cloudfront.net/1715162670.mp4",
        "price": "749.00",
        "discount_price": "719.00",
        "image": "https://ik.imagekit.io/coverimages/tr:w-80/66541dfd2e3fd.png"
    };
    const updateData = async () => {
        console.log("fetchedVideoSettingsData", fetchedVideoSettingsData);
        try {
            await axios.patch(`http://192.168.1.180:3001/updateVideoSettingsByBrandId?brandId=${user.brand_id}`, {
                payload: fetchedVideoSettingsData
            }).then((res) => {
                if (res.data) {
                    toast.success("Updated")
                    getVideoSettingsData()
                    setIsedited(false)

                }
            }).catch((err) => {

                console.log(err);
            })
        } catch (error) {
            console.log(error);
        }

    }
    useEffect(() => {
        const tabs = document.querySelectorAll('.video_settting_tabs .nav-link');
        const tabContent = document.querySelectorAll('.video_settting_tabs_content .tab-pane');
        tabContent.forEach((tc) => {
            console.log(tc.getAttribute('aria-labelledby'));
            if (tc.getAttribute('aria-labelledby') === `left-tabs-example-tab-${activeTab}`) {
                tc.classList.add('show');
                tc.classList.add('active');
            } else {
                tc.classList.remove('show');
                tc.classList.remove('active');
            }
        })
        tabs.forEach(t => {
            if (t.getAttribute('data-rr-ui-event-key') === activeTab) {
                t.classList.add('show');
                t.classList.add('active');
            } else {
                t.classList.remove('show');
                t.classList.remove('active');
            }
        });
    }, [activeTab]);

    const handleSave = (e) => {
        e.preventDefault();

        const eCommTabErrors = () => {
            setActiveTab('e-commerce');
            toast.error("Please fill all required fields in E-commerce tab.");
        };

        if (settings.isBuyEnabled && !settings.isAddtoCartEnabled) {
            const isValid = buttonValues[0].value !== '';
            if (isValid) {
                updateData();
            } else {
                eCommTabErrors();
            }
        } else if (settings.isAddtoCartEnabled) {
            const isValid = buttonValues[1].value !== '';
            if (isValid) {
                if (settings.isCardpopup) {
                    const indicesToCheck = [2, 3, 4];
                    const allValid = indicesToCheck.every(index => buttonValues[index].value !== '');
                    if (allValid) {
                        updateData();
                    } else {
                        eCommTabErrors();
                    }
                } else {
                    updateData();
                }
            } else {
                eCommTabErrors();
            }
        } else {
            updateData();
        }
    };


    const getVideoSettingsData = async () => {
        try {
            await axios.get(`http://192.168.1.180:3001/getSwirlShortCodeInfoByBrandId?brandId=${user.brand_id}`).then((rs) => {
                if (rs.data) {

                    setFetchedVideoSettingsData(rs.data.sscResponse)
                    const data = rs.data.sscResponse
                    const brandCust = data.brandCustomization
                    // data.autoPlayMuteUn && data.autoPlayMuteUn == 1 ? true : false,
                    setSettings({
                        isMuteEnabled: !!data.autoPlayMuteUn,
                        ispipEnabled: !!data.pipMode,
                        ispreviewEnabled: brandCust.next_previous_preview === 1,
                        isvideodownloadEnabled: !!brandCust.download_icon,
                        isproductTagEnabled: !!brandCust.product_tags,
                        iscommentEnabled: !!brandCust.comment_mode,
                        isproductreviewEnabled: !!brandCust.product_reviews,
                        isbuynowEnabled: false,
                        isctaEnabled: !!brandCust.cta_on_tile,
                        isaddtocartEnabled: false,
                        isBuyEnabled: !!brandCust.buy_now,
                        isAddtoCartEnabled: !!brandCust.add_to_cart,
                        isviewcartEnabled: !!brandCust.view_cart,
                        isProductquantityEnabled: !!brandCust.product_qty,
                        isCardpopup: !!brandCust.show_add_to_cart_popup,
                        isAutoplayEnabled: !!brandCust.autoPlay,
                        isViewsEnabled: !!brandCust.views,
                        isTagEnabled: !!brandCust.deals,
                        isProductEnabled: !!brandCust.product_blog_img,
                        isPriceEnabled: !!brandCust.product_price_status,
                        isVideoTitleEnabled: !!brandCust.video_title,

                    })

                    setColorPickers([
                        { id: 1, isVisible: false, color: brandCust.off_fk_color },
                        { id: 2, isVisible: false, color: brandCust.off_bk_color },
                        { id: 3, isVisible: false, color: brandCust.mrp_fk_color },
                        { id: 4, isVisible: false, color: brandCust.dis_fk_color },
                    ])
                    setColorStates([
                        { id: 5, color: brandCust.front_color_buy_btn, showColorPicker: false },
                        { id: 6, color: brandCust.bk_color_buy_btn, showColorPicker: false },
                        { id: 7, color: brandCust.front_color_add_to_cart_btn, showColorPicker: false }
                    ])
                    setButtonValues([
                        { id: 8, value: brandCust.buy_btn },
                        { id: 9, value: brandCust.add_to_cart_btn },
                        { id: 10, value: brandCust.cart_success_message },
                        { id: 11, value: brandCust.view_cart_txt },
                        { id: 12, value: brandCust.shopping_text },
                    ])
                }
            }).catch((err) => {
                console.log(err);
            })
        } catch (error) {
            console.log(error);
        }
    }

    useEffect(() => {
        getVideoSettingsData()
    }, [])


    // useEffect(() => {

    //     setSettings({

    //     })

    // }, [fetchedVideoSettingsData])


    return (
        <Container className='h-100' style={{ border: "1px solid #D1D1D2", borderRadius: "5px" }} fluid>
            <Row className='h-100 ps-2'>
                <Tab.Container id="left-tabs-example" defaultActiveKey={activeTab} onSelect={(k) => setActiveTab(k)}>
                    <Row className='h-100 pt-3 pb-3'>
                        <Col md={2} className=" h-100 mb-3" style={{ borderRight: "1px solid #D1D1D2" }}>
                            <Nav variant="pills" className="flex-column mt-3 video_settting_tabs">
                                <Nav.Item>
                                    <Nav.Link className="p-3" eventKey="playlist">Playlist</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link className="p-3" eventKey="e-commerce">E-commerce</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link className="p-3" eventKey="video-player">Video player</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link className="p-3" eventKey="product-detail-page">Product detail page</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link className="p-3" eventKey="other-page">Other Settings</Nav.Link>
                                </Nav.Item>
                            </Nav>
                        </Col>
                        <Col md={activeTab === 'product-detail-page' && 10 || activeTab === 'other-page' && 7} className="p-3 h-100 video_settting_tabs_content">
                            <form style={{ height: "calc(100% - 50px)" }} onSubmit={(e) => { handleSave(e) }}>
                                <Tab.Content className='h-100' activeKey={activeTab}>
                                    <Tab.Pane eventKey="playlist" className='h-100'>
                                        <h3 className="fs-24 mt-1 pb-2">General settings</h3>
                                        <PlaylistSetting handleSwitchChnage={handleSwitchChnage} handleColorChangeInPlaylist={handleColorChangeInPlaylist} settings={settings} setSettings={setSettings} colorPickers={colorPickers} setColorPickers={setColorPickers} />
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="e-commerce" className='h-100'>
                                        <h5 className="fs-24 mt-1 pb-2">Product details</h5>
                                        <Ecommerce isSeenEnabled={isSeenEnabled} setIsSeenEnabled={setIsSeenEnabled} handleBtnValuesChnage={handleBtnValuesChnage} handleSwitchChnage={handleSwitchChnage} handleColorChangeInEcommerce={handleColorChangeInEcommerce} settings={settings} setSettings={setSettings} initialColors={initialColors} setColorStates={setColorStates} colorStates={colorStates} buttonValues={buttonValues} setButtonValues={setButtonValues} />
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="video-player" className='h-100'>
                                        <h5 className="fs-24 mt-1 pb-2">General Settings</h5>
                                        <VideoplayerSetting handleSwitchChnage={handleSwitchChnage} settings={settings} setSettings={setSettings} />
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="product-detail-page" className='h-100'>
                                        <h5 className="fs-24 mt-1 pb-2">Product page videos</h5>
                                        <Productdetailpage />
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="other-page" className='h-100'>
                                        <h5 className="fs-24 mt-1 pb-2">Other Settings</h5>
                                        <Othersetting />
                                    </Tab.Pane>
                                </Tab.Content>
                                {activeTab != 'product-detail-page' && <button type='submit' className='btn btn-dark mt-4'> Save</button>}
                            </form>
                        </Col>
                        {activeTab == 'playlist' && (
                            <Col md={5} className='h-100'>
                                <div className="container h-100">
                                    <div className="row justify-content-center ps-4 pe-4">
                                        <div className='col-6 h-100'>
                                            <PlaylistCard videoData={videoData} settings={settings} setSettings={setSettings} colorPickers={colorPickers} setColorPickers={setColorPickers} /></div>
                                        <div className='col-6 h-100'> <PlaylistCard videoData={videoData} settings={settings} setSettings={setSettings} colorPickers={colorPickers} setColorPickers={setColorPickers} /></div>
                                    </div>
                                </div>
                            </Col>
                        )}
                        {activeTab == 'e-commerce' && (
                            <Col md={5} className='h-100'>
                                <div className="container h-100">
                                    <div className="row justify-content-center h-100">
                                        <EcommerceCard isSeenEnabled={isSeenEnabled} setIsSeenEnabled={setIsSeenEnabled} settings={settings} videoData={videoData} setButtonValues={setButtonValues} buttonValues={buttonValues} colorStates={colorStates} initialColors={initialColors} />
                                    </div>
                                </div>
                            </Col>
                        )}
                        {activeTab == 'video-player' && (
                            <Col md={5}>
                                <div className="container">
                                    <div className="row justify-content-center ps-4 pe-4">
                                        <VideoPlayerCard videoData={videoData} settings={settings} setSettings={setSettings} setButtonValues={setButtonValues} buttonValues={buttonValues} colorStates={colorStates} />
                                    </div>
                                </div>
                            </Col>
                        )}
                    </Row>
                </Tab.Container>
            </Row>
        </Container>
    );
};

export default VideoSettingsTabs;