
import React, { useState, useRef, useEffect } from 'react';
import { IoColorPaletteOutline } from 'react-icons/io5';
import { ChromePicker } from 'react-color';
import './playlistsetting.css';
import { OverlayTrigger, Popover } from 'react-bootstrap';


const PlaylistSetting = ({ handleSwitchChnage, handleColorChangeInPlaylist, settings, setSettings, colorPickers, setColorPickers }) => {

  const handleToggle = (key) => {

    setSettings((prevSettings) => {
      let updatedSettings = {
        ...prevSettings,
        [key]: !prevSettings[key],
      };

      // Mutual exclusivity logic
      if (key === 'isProductEnabled' && updatedSettings.isProductEnabled) {
        updatedSettings.isVideoTitleEnabled = false;
      }
      if (key === 'isVideoTitleEnabled' && updatedSettings.isVideoTitleEnabled) {
        updatedSettings.isProductEnabled = false;
      }
      handleSwitchChnage(updatedSettings)
      return updatedSettings;
    })

  };


  const handleColorChange = (id, color) => {
    setColorPickers((prevColorPickers) => {
      const chnageCOlors = prevColorPickers.map((picker) =>
        picker.id === id ? { ...picker, color: color.hex } : picker
      )

      return chnageCOlors
    }
    );
  };

  useEffect(() => {
    let timer;
    timer = setTimeout(() => {
      handleColorChangeInPlaylist(colorPickers)
    }, 400);
    return () => clearTimeout(timer);
  }, [colorPickers]);

  const popover = (id) => {

    const colorPicker = colorPickers.find((picker) => picker.id === id);


    return (
      <Popover style={{ margin: "0", padding: '0' }}>
        <Popover.Body>
          <ChromePicker
            color={colorPicker ? colorPicker.color : '#ffffff'}
            onChange={(color) => handleColorChange(id, color)}
          />
        </Popover.Body>
      </Popover>
    );
  };

  return (
    <div style={{ height: "calc(100% - 40px)", overflowY: "auto", overflowX: "hidden", scrollBehavior: "smooth" }}>
      <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0">
        <div>
          <label className="fs-18 form-check-label ps-2" htmlFor="autoplaySwitch">
            Autoplay
            <br />
            <div className="fs-16">
              Automatically play your playlist videos preview with this feature.
            </div>
          </label>
        </div>
        <div>
          <input
            className="form-check-input switch-lg"
            type="checkbox"
            role="switch"
            id="autoplaySwitch"
            checked={settings.isAutoplayEnabled}
            onChange={() => handleToggle('isAutoplayEnabled')}
          />
        </div>
      </div>

      <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0">
        <div>
          <label className="fs-18 form-check-label ps-2" htmlFor="viewsSwitch">
            Total views
            <br />
            <div className="fs-16">See the number of views for each video in your playlist.</div>
          </label>
        </div>
        <div>
          <input
            className="form-check-input switch-lg"
            type="checkbox"
            role="switch"
            id="viewsSwitch"
            checked={settings.isViewsEnabled}
            onChange={() => handleToggle('isViewsEnabled')}
          />
        </div>
      </div>

      <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0">
        <div>
          <label className="fs-18 form-check-label ps-2" htmlFor="tagSwitch">
            Product tag
            <br />
            <div className="fs-16">This option enables product tagging for attached items.</div>
          </label>
        </div>
        <div>
          <input
            className="form-check-input switch-lg"
            type="checkbox"
            role="switch"
            id="tagSwitch"
            checked={settings.isTagEnabled}
            onChange={() => handleToggle('isTagEnabled')}
          />
        </div>
      </div>

      <div>
        <h3 className="mt-3 pb-2" style={{ borderBottom: '1px solid #E8E8E8', fontSize: '24px', color: '#454549' }}>
          Product section
        </h3>
        <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0">
          <div>
            <label className="fs-18 form-check-label ps-2" htmlFor="productSwitch">
              Product Card
              <br />
              <div className="fs-16">This option enables product attached items.</div>
            </label>
          </div>
          <div>
            <input
              className="form-check-input switch-lg"
              type="checkbox"
              role="switch"
              id="productSwitch"
              checked={settings.isProductEnabled}
              onChange={() => handleToggle('isProductEnabled')}

            />
          </div>
        </div>

        {settings.isProductEnabled && (
          <div style={{ backgroundColor: settings.isPriceEnabled ? '#f0f0f0' : '#ffffff', borderRadius: '5px' }}>
            <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0">
              <div>
                <label className="fs-18 form-check-label ps-2" htmlFor="priceSwitch">
                  Product Price
                  <br />
                  <div className="fs-16">View the product price along with various other options.</div>
                </label>
              </div>
              <div>
                <input
                  className="form-check-input switch-lg"
                  type="checkbox"
                  role="switch"
                  id="priceSwitch"
                  checked={settings.isPriceEnabled}
                  onChange={() => handleToggle('isPriceEnabled')}
                />
              </div>
            </div>

            {settings.isPriceEnabled && (
              <div className="mt-3 pb-2 ps-2">
                <div className="row">
                  {colorPickers.map((picker) => (
                    <div key={picker.id} className="col-5">
                      <label
                        className="mt-1 mb-1"
                        style={{ fontWeight: 'lighter', fontSize: '13px', color: '#454549' }}
                      >
                        {picker.id === 1 && 'Discount text color'}
                        {picker.id === 2 && 'Background color'}
                        {picker.id === 3 && 'MRP text color'}
                        {picker.id === 4 && 'Selling background color'}
                      </label>
                      <OverlayTrigger trigger="click" overlay={popover(picker.id)} rootClose>
                        <div className="input-group mb-3">
                          <span className="input-group-text">
                            <span className="color_badge" style={{ backgroundColor: picker.color }}></span>
                          </span>
                          <input
                            type="text"
                            value={picker.color}
                            // onClick={() => handleClick(picker.id)}
                            readOnly
                            className="form-control pointer_cursor"
                          />
                          <span className="input-group-text pointer_cursor" >
                            <IoColorPaletteOutline />
                          </span>
                        </div>
                      </OverlayTrigger>

                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        )}
      </div>

      <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0">
        <div>
          <label className="fs-18 form-check-label ps-2" htmlFor="videoTitleSwitch">
            Video Title
            <br />
            <div className="fs-16">Display the video title set at the time of upload.</div>
          </label>
        </div>
        <div>
          <input
            className="form-check-input switch-lg"
            type="checkbox"
            role="switch"
            id="videoTitleSwitch"
            checked={settings.isVideoTitleEnabled}
            onChange={() => handleToggle('isVideoTitleEnabled')}

          />
        </div>
      </div>
    </div>
  );
};

export default PlaylistSetting;

